import { SN_PAR_POPOVER_CLOSE_REQUEST } from '../sn-par-popover.constants';

const closeRequestListenersTrack = new Map();

const documentCloseRequestHandler = (...args) => {
	closeRequestListenersTrack.forEach(fn => fn(...args));
};

export const addCloseRequestListener = (componentId, fn) =>
	closeRequestListenersTrack.set(componentId, fn);

export const removeCloseRequestListener = componentId =>
	closeRequestListenersTrack.delete(componentId);

/**
 * There is only one actual listener, that will execute
 * all the internal listeners registered in the Map.
 * This avoid the need of removing/adding the actual listener multiple times.
 */
document.addEventListener(
	SN_PAR_POPOVER_CLOSE_REQUEST,
	documentCloseRequestHandler
);
