// https://code.devsnc.com/dev/design-system/blob/ee6b8db1e05cc74ef4298f5f9b152fc9ffaaa011/components/popover/src/utils/global-event-listener.js

import isEmpty from 'lodash/isEmpty';
import forEach from 'lodash/forEach';
import debounce from 'lodash/debounce';

export function createGlobalListener(eventType, { batch = false } = {}) {
	const callbacks = {};

	let runCallbacks = (event) => forEach(
		callbacks,
		(callback) => callback(event)
	);

	if (batch) {
		runCallbacks = debounce(runCallbacks, 500);
	}

	return {
		add(id, callback) {
			const needsToAttach = isEmpty(callbacks);

			callbacks[id] = callback;

			if (needsToAttach) {
				window.addEventListener(eventType, runCallbacks, true);
			}
		},

		remove(id) {
			delete callbacks[id];
			const canDetach = isEmpty(callbacks);

			if (canDetach) {
				window.removeEventListener(eventType, runCallbacks, true);
			}
		}
	};
}
