// @jsx snabbdom.createElement
import { createCustomElement } from '@servicenow/ui-core';
import overlayBehavior from '@servicenow/behavior-overlay';
import snabbdom from '@servicenow/ui-renderer-snabbdom'; // eslint-disable-line no-unused-vars
import { AREA_CLASS } from './sn-par-popover.constants';
import styles from './sn-par-popover.scss';
import actions from './sn-par-popover.actions';

createCustomElement('sn-par-popover', {
	renderer: { type: snabbdom },
	view: ({ componentId, fitBehavior, properties: { opened, showArrow } }) => {
		const behaviorPosition = (fitBehavior.detail.targetPosition || []).join('-');

		if (!opened) {
			return null;
		}

		return (
			<div
				class={{
					[AREA_CLASS]: true,
					[`${AREA_CLASS}--hidden`]: !fitBehavior.style.top
				}}
				style={{ top: fitBehavior.style.top, left: fitBehavior.style.left }}>
				{ showArrow
					? <span
						className={`sn-par-popover__arrow sn-par-popover__arrow--${behaviorPosition}`}
					/>
					: undefined
				}
				<div className="sn-par-popover__content" id={`popover-${componentId}`}>
					<slot />
				</div>
			</div>
		);
	},
	styles,
	initialState: {
		fitBehavior: {
			style: {},
			detail: {},
		},
	},
	behaviors: [{ behavior: overlayBehavior }],
	properties: {
		/**
		 * @type {boolean}
		 */
		opened: { default: false, manageable: true },
		/**
		 * If `true`, popover will trap the global focus inside the content,
		 * Trying to find the next element to focus.
		 * @type {boolean}
		 */
		showArrow: { default: true },
		/**
		 * If `true`, popover will trap the global focus inside the content,
		 * Trying to find the next element to focus.
		 * @type {boolean}
		 */
		trapFocus: { default: false },
		/**
		 * The popover appears when the user hovers over or tabs to this target HTML element.
		 * @type {HTMLElement}
		 */
		targetRef: {},
		/**
		 * Element that the popover must fit within.
		 *
		 * @type {HTMLElement}
		 */
		container: {},
		/**
		 * Array of possible positions to place the popover relative to its target.
		 *
		 * @see Fit behavior for valid values.
		 * @type {Array<string>}
		 */
		position: {
			default: [
				'top-center bottom-center',
				'bottom-center top-center',
				'center-end center-start',
				'center-start center-end',
				'top-end top-start',
				'bottom-end bottom-start',
				'top-start top-end',
				'bottom-start bottom-end',
			],
		},
		/**
		 * Pixel amount to relatively shift the popover from its target.
		 *
		 * @see Fit behavior for valid values.
		 * @type {Array<number>}
		 */
		offset: {
			default: [0, 0, 10, 13, 10, 13],
		},
	},
	...actions,
});
