
/**
 * Constraints that will be used for the Fit Behavior
 */
export const FIT_CONSTRAIN = { minHeight: 24, maxWidth: 1280 };
/**
 * Element class for the popover wrapper
 */
export const AREA_CLASS = 'sn-par-popover-content-area';
/**
 * Action that is fired when external factors force the popover to
 * set the `opened` value, so wrapper components can change the state
 */
export const SN_PAR_POPOVER_OPENED_SET = 'SN_PAR_POPOVER#OPENED_SET';

/**
 * Global Event name.
 * Should it be dispatched from `document`, all popovers should close
 */
export const SN_PAR_POPOVER_CLOSE_REQUEST = 'sn-par-popover:close-request';
