import { getBestFitInfo } from '@servicenow/behavior-fit';
import { actionTypes } from '@servicenow/ui-core';
import isEqual from 'lodash/isEqual';

import { findNextFocusTarget } from './utils/focus';
import { createGlobalListener } from './native-event-handlers/global-event-listener';

import {
	AREA_CLASS,
	FIT_CONSTRAIN,
	SN_PAR_POPOVER_OPENED_SET,
	SN_PAR_POPOVER_CLOSE_REQUEST
} from './sn-par-popover.constants';

import {
	addOutsideScrollListener,
	removeOutsideScrollListener,
} from './native-event-handlers/outside-scroll';

import {
	addCloseRequestListener,
	removeCloseRequestListener,
} from './native-event-handlers/close-request';

const onResize = createGlobalListener('resize');
const onKeydown = createGlobalListener('keydown');

export const dispatchCloseRequestEvent = (componentId) => {
	const event = new CustomEvent(SN_PAR_POPOVER_CLOSE_REQUEST, { detail: { componentId } });
	document.dispatchEvent(event);
};

const {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_DISCONNECTED,
	COMPONENT_PROPERTY_CHANGED,
} = actionTypes;

export default {
	actions: {
		'PRIVATE#REFIT': {
			private: true,
		},
	},
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({ state, updateState, action, dispatch }) => {
			const {
				payload: { host },
				meta: { id },
			} = action;

			host.refit = () => dispatch('PRIVATE#FIT');

			updateState({ host: host });

			if (state.opened) {
				dispatchCloseRequestEvent(id);

				addCloseRequestListener(id, (evt) => {
					dispatch(SN_PAR_POPOVER_OPENED_SET, { value: false });
				});

				addOutsideScrollListener(host, id, () => {
					dispatch(SN_PAR_POPOVER_OPENED_SET, { value: true });
				});
			}
		},

		[COMPONENT_DISCONNECTED]: ({ action }) => {
			const {
				payload: { host },
				meta: { id },
			} = action;

			removeOutsideScrollListener(host);
			removeCloseRequestListener(id);
			onKeydown.remove(id);
			onResize.remove(id);
		},

		/**
		 * Every time opened or other property changes, it's necessary
		 * to check the postion. The PRIVATE#FIT action will later
		 * check if it's necessary to re-calculate positions,
		 * but we also need to take into account the scroll events.
		 */
		[COMPONENT_PROPERTY_CHANGED]: ({ state, dispatch, action }) => {
			const {
				payload: { name, value, host },
				meta: { id },
			} = action;

			dispatch('PRIVATE#FIT', {});

			if (name !== 'opened') {
				return;
			}

			if (value) {
				const triggerOpenedSetAction = (evt) => {
					if (!evt.detail || evt.detail.componentId !== id) {
						dispatch(SN_PAR_POPOVER_OPENED_SET, { value: false });
					}
				}

				const handleKeydown = (event) => {
					if (event.key === 'Escape') {
						dispatch(SN_PAR_POPOVER_OPENED_SET, { value: false });
					}

					if (event.key === 'Tab' && state.properties.trapFocus) {
						event.preventDefault();

						const focusTarget = findNextFocusTarget({
							within: host.shadowRoot.querySelector(`.${AREA_CLASS}`),
							shiftKey: event.shiftKey
						});

						if (focusTarget) {
							focusTarget.focus();
						}
					}
				};

				dispatchCloseRequestEvent(id);

				addOutsideScrollListener(host, id, triggerOpenedSetAction);
				addCloseRequestListener(id, triggerOpenedSetAction);
				onResize.add(id, triggerOpenedSetAction);
				onKeydown.add(id, handleKeydown);
			} else {
				removeCloseRequestListener(id);
				removeOutsideScrollListener(host);
				onKeydown.remove(id);
			}
		},

		'OVERLAY_BEHAVIOR#OPENED_SET': ({ dispatch }) => {
			dispatch(SN_PAR_POPOVER_OPENED_SET, { value: false });
		},

		/**
		 * Action that actually calculates the correct position
		 * the popover should be, based on the target, using fit behavior code.
		 * This is private and should only be triggered/handled internally.
		 */
		'PRIVATE#FIT': ({ state, action, updateState }) => {
			const {
				meta: { id },
			} = action;

			const {
				opened,
				targetRef: target,
				container,
				position,
				offset,
			} = state.properties;

			const host = state.host;

			const content = host.shadowRoot.querySelector(`#popover-${id}`);
			const contentArea = host.shadowRoot.querySelector(`.${AREA_CLASS}`);
			const isVisible = !!(opened && target);

			const fitParams = {
				target,
				content,
				contentArea,
				container,
				position,
				offset,
				constrain: FIT_CONSTRAIN,
			};

			const fitBehavior = isVisible
				? getBestFitInfo(fitParams)
				: { style: {}, detail: {} };

			if (!isEqual(fitBehavior, state.fitBehavior)) {
				updateState({ fitBehavior });
			}
		},
	},
};
